@import "variables";



.form{
    position: relative;
    margin: 1rem;
    
    label{
        color:#919191; 
        font-size:1rem;
        font-family: $primary-font;
        position:absolute;
        pointer-events:none;
        left:5px;
        top:.35rem;
        transition:0.2s ease all; 
      }
    input:focus ~ label{
        top:-1.25rem;
        font-size:.85rem;
        color:#5264AE;
    
    }
    .bar  { 
        position:relative; 
        display:block; 
    }
    .bar:before, .bar:after{
        content:'';
        height:2px; 
        width:0;
        bottom:1px; 
        position: absolute;
        background:#5264AE; 
        transition:0.2s ease all; 
    }
    .bar:before {
        left:50%;
    }
    .bar:after {
        right:50%; 
    }

    input:focus ~ .bar:before, input:focus ~ .bar:after {
        width:50%;
    }

    .highlight {
        position:absolute;
        height:60%; 
        width:100px; 
        top:25%; 
        left:0;
        pointer-events:none;
        opacity:0.5;
      }
      
      input:focus ~ .highlight {
        animation:inputHighlighter 0.3s ease;
      }
      
      @keyframes inputHighlighter {
        from  { background:#5264AE; }
        to    { width:0; background:transparent; }
      }
      svg{
          position: absolute;
          top: .2rem;
          left: 39rem;
          fill: #919191;
          height: 1.5rem;
          width: 1.5rem;
      }
}

.search{
    font-size: 1rem;
        padding: .25rem;
        display: block;
        border: none;
        border-bottom: 2px solid #919191;
        box-shadow: none;
        -webkit-appearance: none;
        border-radius: 0px;
        -webkit-border-radius:0px;
        background: none;
}

.darkModeSearch{
        font-size: 1rem;
        padding: .25rem;
        display: block;
        border: none;
        border-bottom: 2px solid #919191;
        color: $text-color-light;
        box-shadow: none;
        -webkit-appearance: none;
        border-radius: 0px;
        -webkit-border-radius:0px;
        background: none;
}