@import "variables";

.reactToggle {
  height: 0;
  width: 0;
  visibility: hidden;
}

.reactToggleLabel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  width: 4rem;
  height: 2rem;
  background: grey;
  border-radius: 100px;
  position: relative;
  transition: background-color 0.2s;
}

.reactToggleLabel .reactButton {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 1.75rem;
  height: 1.75rem;
  border-radius: 45px;
  transition: 0.2s;
  background: #fff;
  box-shadow: 0 0 2px 0 rgba(10, 10, 10, 0.29);
}

.reactToggle:checked + .reactToggleLabel .reactButton {
  left: calc(100% - 2px);
  transform: translateX(-100%);
}

.reactToggleLabel:active .reactButton {
  width: 60px;
  color: green($color: #000000);
}
